import React, {Component} from 'react';
import logo from './img/standup_logo.png';
import './App.css';
import Editor from './Editor';
import config from "./config";
import FirebaseDao from "./FirebaseDao";

console.log(config.apiKey);

class App extends Component {
    constructor() {
        super();
        this.handleSubmit = this.handleSubmit.bind(this);
        this.dao = new FirebaseDao(config);
        this.submit = this.submit.bind(this);
        this.getArticles = this.getArticles.bind();
        this.state = {
            articles: []
        }
    }

    submit(article) {
        if (article) {
            let key = this.dao.newKey();
            let updated = this.dao.update(key, article);
            return updated;
        }
    }

    getArticles(){
        let lis = [];
        for(let i=0;i<this.state.articles.length;i++){
            lis.push(<li key={this.state.articles[i].key}>{this.state.articles[i].content}</li>);
        }
        return lis;
    }

    // 버튼을 눌렀을 때 Action. 우선은 console.log로 확인
    handleSubmit(e) {
        console.log(this, e);
    }

    // 익명 사용자 여부
    isAnonymous() {
        return true;
    }

    render() {
        return (
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo"/>
                    <h2>Stand Up 앱을 만들어 봅시다</h2>
                </header>
                <Editor {...this} />
            </div>
        );
    }
}

export default App;
